@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  padding: 0;
  margin: 0;
  box-shadow: none;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F2E085;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --green-dark :  #025928;
  --green-light: #758C1F;
  --yellow-dark:#F2DA5E;
  --yellow-light : #F2E085; 
  --brown:  #8C4F2B;
  --link-dark: #024059;
  --link-light: #84C1D9;
  --white: #FDFDE8;
  --black: #262626;
  --blueLight : #84C1D9 ;
  --blueDark : #024059;
}

.App {
  text-align: center;
}

.form-container{
  margin: left;
  margin: 20px 20px;
  padding: 50px;
  background-color: #FDFDE8;
  background-color: var(--white);
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.form-container .send-button{
  margin-right: 0;
  margin-left: auto;
  display: block;
}

.plant{
  width: 200px;
}

.porcentaje{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 98vh;
  background: #FDFDE8;
  background: var(--white);
}

.container{
  position: relative;
  width: 960px;
  display: flex;
  justify-content: space-around;
}

.nav{
  width: 100%;
  display: flex;
  justify-content: space-around;
  background-color: #8C4F2B;
  background-color: var(--brown);
  margin-left: auto;
}

.logo{
  padding: 5px;
  padding-right: 60px;
  height: 60px;
}

.nav .h2{
  color: #FDFDE8;
  color: var(--white);
  text-decoration: none;
  -webkit-text-decoration-line: #FDFDE8;
          text-decoration-line: #FDFDE8;
  -webkit-text-decoration-line: var(--white);
          text-decoration-line: var(--white);
}

.active{
  display: block;
}

.active{
  display: none;
}
